import { formatAsMinSecs } from "../utils"

// defines which assessment steps are not required per scenario.  Provides feedback on
// why the step is not required.
export const exceptionDefs = 
[{scenarioName: "M2CA", exceptions: [["assess-opqrst-info", "This cannot be assessed when GCS < 15 "]]}
  ]

const colorTypes = {
    "Good": "text-success",
    "Caution": "text-warning",
    "Problem": "text-danger",
    "Not implemented": "text-muted"
}

const iconTypes = {
    "Good": "sentiment_satisfied",
    "Caution": "report_problem",
    "Problem": "highlight_off",
    "Not implemented": "sentiment_neutral"
}

const assessmentEntries = {
    // state, assessement value, default feedback, list of fields in input entry with specialized feedback 
    "contraindicated": ["Problem", "This is contraindicated", ["contraindicatedWhy"]],
    "missingMinimal": ["Problem", "This is recommended by protocol", ["protocol", "why"]],
    "missingOptional": ["Caution", "It is optional to do this", ["protocol", "why"]],
    "missingRequired": ["Problem", "This is recommended by protocol", ["protocol", "why"]],
    "missingStandard": ["Problem", "This is recommended by protocol", ["protocol", "why"]],
    "missingAssessment": ["Problem", "Did not do this assessment", []],
    "notNecessary": ["Caution", "This was unnecessary.", ["why"]],
    "assessment": ["Good", "", []],
    "assessment-option": ["Caution", "System Issue.  Not yet checking correctness of related options", []],
    "decision-option": ["Caution", "System Issue.  Not yet checking correctness of decision options", []],
    "required-action": ["Good", "", []],
    "assessment-option-incorrect": ["Caution", "You chose the wrong assessment option", []],
    "decision-option-incorrect": ["Caution", "You chose the wrong decision option", []],
    "standard": ["Good", "", []],
    "standard-incorrect-answers": ["Caution", "The intervention is correct but there is a problem with the details you provided", []],
    "minimal": ["Caution", "You solved the problem but did not follow protocol", ["minimalWhy"]],
    "green": ["Good", "", []],  //just for headers
    "red": ["Problem", "", []], //just for headers
    "yellow": ["Caution", "", []], //just for headers
    //"notFound": ["Not implemented", "Assessment knowledge is not yet specified", []],
    "default": ["Not implemented", "Possible system issue.  This was not noted in the expert solution", []]
}

const getAssessmentEntry = (currentEntry,aIndex) => {
    let assessmentEntry = ""
    if (!assessmentEntries[currentEntry.status]){
        assessmentEntry = assessmentEntries["default"]}
    else{assessmentEntry = assessmentEntries[currentEntry.status]}
    return assessmentEntry[aIndex];
}

export const getAssessmentIcon = (currentEntry) => {  
    let val = iconTypes[getAssessmentEntry(currentEntry,0)]
    return val;
}

export const getColor = (currentEntry) => {
    return colorTypes[getAssessmentEntry(currentEntry,0)];
}

export const getFeedback = (currentEntry) => {
    let defaultFeedback = getAssessmentEntry(currentEntry,1)
    let specificFeedbackFields = getAssessmentEntry(currentEntry,2)
    //console.log("fields are", specificFeedbackFields)
    let specificFeedback = ""
    for (let field of specificFeedbackFields){
        //console.log(field, currentEntry[field], currentEntry["protocol"], currentEntry["why"])
        if (!!currentEntry[field]){
            if (specificFeedback !== ""){
               specificFeedback = specificFeedback + ".  " + currentEntry[field]}
            else {specificFeedback = currentEntry[field]}
        }
    }
    if (defaultFeedback && specificFeedback) {return defaultFeedback + " " + specificFeedback}
    return defaultFeedback || specificFeedback
}
    


export const getTimestamp = (currentEntry) => {
    if (currentEntry.status === "criticalActionHeader") {
        return "";
    }
    if (currentEntry.timestamp === -1) {
        return "N/A";  // formatAsMinSecs would convert this to 59:59
    }
    return formatAsMinSecs(currentEntry.timestamp);
}
