import React, { useState, useEffect } from 'react';

import { Button, Table } from 'reactstrap';

import MDBNavBar from "./MDBNavBar"

import { MDB_TYPES } from '@meddbriefer/scenario-data/constants.js';

import { getAssessmentIcon, getColor, getFeedback, getTimestamp, exceptionDefs } from "./debriefingUtils"

import { db } from "@meddbriefer/mdb-firebase"

const _ = require("lodash"); 

// const Debriefing = ({ log, metaData, criticalActions, associations }) => {
const Debriefing = ({ log, scenario }) => {
    const [showCommentColumn, setShowCommentColumn] = useState(false);
    // eslint-disable-next-line
    const [events, setEvents] = useState(log.events);
    const [formattedActions, setFormattedActions] = useState(null);
    // Set to true once all performed actions have been displayed in the log
    // const [finishedPerformedActions, setFinishedPerformedActions] = useState(false);

    //preprocess some of data from problems/solutions
    //and for now just flatten it some to set up to 
    //look more like the original criticalActions in scenario
    // assumes just one solution for now.
    let problemDefs = scenario.problems 
    //console.log(problemDefs);
    let problems = [];
    for (let problemDef of problemDefs){
        let problem = {};
        problem.findings = problemDef.findings
        problem.allProblemProtocols = problemDef.protocol  
        problem.probLabel = problemDef.problem   
        problem.critical = false;
        if (problemDef.type === "critical"){problem.critical = true};
        let solution = problemDef.solutions[0];
        problem.actions = solution.actions
        problem.contraindications = solution.contraindications
        problem.minimalWhy = solution.minimalWhy
        problem.protocols = solution.protocols
        problems.push(problem)}

        //console.log(problems)

    //for now just dealing with the first solution for each problem
     

    const toggleCommentColumn = () => {
        setShowCommentColumn(!showCommentColumn);
    }

    const updateFormattedAction = (record) => {
        const index = formattedActions.findIndex(rec => rec.id === record.id)
        setFormattedActions([
            ...formattedActions.slice(0, index),
            record,
            ...formattedActions.slice(index+1)
        ])
    }

    const saveDebriefingLog = () => {
        const docRef = db.collection("debriefing-feedback-logs").doc()
        let data = {
            ObserverFileCreater: log.userName,
            ObserverFileName: log.label,
            commenter: "",
            scenarioName: log.scenarioName,
            timestamp: Date().toLocaleString()
        }

        let newActionsList = [];
        let newObj = {};
        let filterUnknown = false;

        //for debugging puposes only
        formattedActions.forEach(function (logObj, index){
            for (let [key, val] of Object.entries(logObj)) {
                if (undefined === val) {
                    console.log(index, key, val)
                    filterUnknown = true;}
                 }
            }
        )
            
        if (filterUnknown) {
            formattedActions.forEach(function (logObj, index) {
                newObj = _.pickBy(logObj, function (value, key) {
                    return !(value === undefined);
                });
                newActionsList.push(newObj)
            })
            data["events"] = newActionsList
        }
        else (data["events"] = formattedActions);
    

        //data["events"] = formattedActions;
        //console.log(JSON.stringify(data, null, 4))
        docRef.set(data) 
    }

    /* const insertInPhase = (phaseNames, phases, object) => {
        let phase = object.phase;
        let phaseIndex = phaseNames.lastIndexOf(phase);
        let phaseObjects = phases[phaseIndex];
        if (!phaseObjects) { phaseObjects = []; }
        phaseObjects.push(object);
        phases[phaseIndex] = phaseObjects;
    } */

    const insertInPhaseInOrder = (phaseNames, phases, object, objIndex) => {
        let phase = object.phase;
        let phaseIndex = phaseNames.lastIndexOf(phase);
        let phaseObjects = phases[phaseIndex];
        if (!phaseObjects) { phaseObjects = []; }
        phaseObjects.splice(objIndex - 1, 0, object);
        phases[phaseIndex] = phaseObjects;
    }

// this is a hack to approximate a good placement for a missing intervention based on ordering of interventions
// in the problems definitions that appear in the log and are just before and just after
// the item to be inserted and what is marked as required that is in the log just before and after.  An approach that
// utilizes partial ordering constraints would be better.  Also it is not clear what to do for really badly ordered
// actions on the part of the student.  Note that it only looks for two anchor points before and two anchor points after
// rather than all potential anchor points.  Also does not deal with a phase that is split into multiple within a log
    const insertInPhaseRelativeToSuggestedOrder = (phaseNames, phases, object, orderedArrayObjects, objIndex) => {
        let objectsBefore = orderedArrayObjects.slice(0, objIndex-1)
        let objectBefore = orderedArrayObjects[objIndex-1];
        // console.log("Objects before in def", {...objectsBefore})
        let requiredObjectBefore = objectsBefore.reverse().find(entry => entry.required)
        let objectAfter = orderedArrayObjects[objIndex + 1];
        let objectsAfter = orderedArrayObjects.slice(objIndex + 1)
        let requiredObjectAfter = objectsAfter.find(entry => entry.required)
        let phaseIndex = phaseNames.lastIndexOf(object.phase);
        let phaseObjects = [...phases[phaseIndex]];  //trying for a deep copy of the array mainly for debug via console.log
        let indexBefore = -1
        let indexAfter = -1
        let indexRequiredBefore = -1
        let indexRequiredAfter = -1
        let insertionIndex = false;
        if (objectBefore) { indexBefore = phaseObjects.findIndex(entry => entry.id === objectBefore.id); }
        if (objectAfter) { indexAfter = phaseObjects.findIndex(entry => entry.id === objectAfter.id); }
        if (requiredObjectBefore) { indexRequiredBefore = phaseObjects.findIndex(entry => entry.id === requiredObjectBefore.id); }
        if (requiredObjectAfter) { indexRequiredAfter = phaseObjects.findIndex(entry => entry.id === requiredObjectAfter.id); }
        if (objectAfter) { indexAfter = phaseObjects.findIndex(entry => entry.id === objectAfter.id); }
        /* console.log("Phase Objects", {...phaseObjects})
        console.log("Object to insert", {...object})
        console.log("Object before",{...objectBefore})
        console.log("Object After",{...objectAfter})
        console.log("Required Object Before", {...requiredObjectBefore})
        console.log("Required Object After", {...requiredObjectAfter}) */
        

        // There is a more compact way to do the below but this made
        // it easier for me to get the logic right

        // insert according to required before first as we want to follow something that was required
        if (indexRequiredBefore !== -1){ 
            insertionIndex = indexRequiredBefore + 2;
            // but if the just before object is greater then insert relative to it
            if (indexBefore > indexRequiredBefore){insertionIndex = indexBefore + 2} 
        }
        //if no required before and not required after then insert relative to
        //required item after in actions definition
        if (!insertionIndex && indexRequiredAfter !== -1) {
            insertionIndex = indexRequiredAfter + 1
            // unless the just after is smaller then override and insert relative to it
            if (indexAfter < indexRequiredAfter){insertionIndex = indexAfter + 1}
        }
        //otherwide if have a just before insert relative to it
        if (!insertionIndex && indexBefore !== -1) {
            insertionIndex = indexBefore + 2
        }
        //otherwise if have a just after insert relative to it
        if (!insertionIndex && indexAfter !== -1){
            insertionIndex = indexAfter + 1
        }
        //otherwise put at end of phase
        if (!insertionIndex){
            insertionIndex = phaseObjects.length + 2
        }
       insertInPhaseInOrder(phaseNames, phases, object, insertionIndex)
        
    }



    //to display the log using the phase structures accumulated
    // i.e. puts the phases data together in the order in which
    // should be displayed in debriefing log
    const organizeLogDisplay = (phaseNames, phases) => {
        let phaseIndex = -1;
        let displayObjects = [];
        let entryIndex = 1;
        let phaseObjects = [];
        phaseNames.forEach(phase => {
            let problemHeaderColor = "green";
            phaseIndex++;
            phaseObjects = phases[phaseIndex];
            phaseObjects.forEach(phaseObject => {
                if (["contraindicated", "missingAssessment", "missingRequired"].includes(phaseObject.status)) {
                    problemHeaderColor = "red";
                }
                // Red has a higher priority than yellow
                if (["missingStandard", "standard-incorrect-answers"].includes(phaseObject.status) && problemHeaderColor !== "red") {
                    problemHeaderColor = "yellow";
                }
                phaseObject.numericalID = entryIndex;
                entryIndex++;
            })

            let tempHeaderObject = {
                "id": "assessmentHeader",
                "type": "header",
                "numericalID": 1,
                "status": problemHeaderColor,
                "why": "",
                "label": getPhaseLabel(scenario, phase),
                "protocol": ""
            }
            //console.log(phaseObjects);
            displayObjects.push(tempHeaderObject);
            displayObjects = displayObjects.concat(phaseObjects);
        })
        return displayObjects;
    }

    const evalForIncorrectAnswer = (promptID, answerID) => {
        //check each prompt and if is the appropriate one then  
        let answers = scenario.associations[promptID];

        if (Array.isArray(answers)) {
            //returns bool regarding whether answerID NOT in answers
            return !(answers.find(ansId => ansId === answerID))

        }
        else {
            return !(answers === answerID)
        }
    }

    const getCorrectAnswerLabel = (promptID) => {
        let answers = scenario.associations[promptID]
        let answerLabels = "";
        if (Array.isArray(answers)){
            answers.forEach((answer, j) => {
                scenario.intvMetaData.forEach((entry, i) => {
                    if (entry.id === answer){
                        if (answerLabels === ""){
                            answerLabels = entry.label}
                        else {answerLabels = answerLabels + ", "}
                        return
                        }
                    })
                })
            }

        else {
            scenario.intvMetaData.forEach((entry, i) => {
                if (entry.id === answers){
                    answerLabels = entry.label
                }
            })
        }
        return answerLabels;
    }

    const findHeaderFor = (numericalID, log) => {
        let candidateHeader = {};
        let foundHeader = {};
        for (let entry of log) {
            if (entry.id === "problemHeader") {
                candidateHeader = entry
            }
            else {
                if (entry.numericalID === numericalID) {
                    foundHeader = candidateHeader;
                    break;
                }
            }
        }
        return (foundHeader)
    }

    const getPhaseLabel = (scenario, phaseID) => {
        let data = scenario.phaseData;
        let result = data.find(entry => entry.id === phaseID);
        //console.log(result.label)
        return result.label
    }

    const findRequiredPhases = () => {
        let phases = [];
        scenario.phaseData.forEach((entry, i) => {
            if (entry.type === "phase") {
                phases.push(entry.id)
            }
        })
        return phases
    }


    const processEvents = () => {
        // temporarily make metaData and events directly accessible from the developer console for dev purposes
        // window.metaData = metaData
        window.events = events

        let tempFormattedActions = [];
        let tempConfirmedActions = [];
        //instead of a multi-dimensional array, am using an array of phase names e.g. "BSI", "prinary" and

        let requiredPhaseNames = findRequiredPhases();
        let phaseNames = [];
        let phases = [];     //and another array with the actual content for each phase.  I can search phaseNames for the last
        // phases entry of that name and then use that index to retrieve the appropriate phase entried to
        //add on the missing items (assessment or intervention) for that phase
        let phaseObjects = [];
        let priorPhase = "";
        let phaseIndex = 0;

        // sts 08/13/21 the following block of code (terminated by another comment) could be categorizeAndAnnotateActions()

        // created a formated object for each event
        events.forEach((event, i) => {
            // Each tempObject is one of the lines in the displayed log
            // Set up fields for extra annotations for this event after analyzing each event
            let tempObject = {
                "comment": "",  //added through interface by a commenter
                "numericalID": i,  // The number displayed in the "ID" column
                "status": "",  // used by grading process, categorizes interventions as standard, missing-standard, etc.
                "contraindicatedWhy": "",  // Feedback
                "minimalWhy": "",  // Feedback
                "why": "",  // Feedback
                "displayLabel": "",  // not yet in use
                "answerCorrect": true, // applicable only for a question asked, default is true
                "protocol": "", // collected from the problem solution definitions
                "probLabel": "", // Label to display for a group of interventions related to a problem
                "findings": "", // list of findings needed to recognize problem intervention is addressing
                "required": false, // determined based on actions marked as required to solve problem
                "actionDescription": "",  // for the debriefing display as determined by analysis but will default to event.label for debugging below
                "processingState": "good" // values of good/bad used to filter out events not needed for analysis or displaying
            }

            // copy over the event fields that exist in the input log from observer UI (which is type dependent)
            const EVENT_FIELDS = ["id", "label", "finding", "phase", "subPhase", "vital", "value", "type", "prompt", "intervention", "timestamp"]
            EVENT_FIELDS.forEach(fldName => {
                if (!!event[fldName]) {
                    tempObject[fldName] = event[fldName]
                }
            })

            //copy event fields if defined to initialize some of the above annotation fields
            if (!!event.label){tempObject.actionDescription = event.label}
            if (!!event.type){tempObject.status = event.type}

            //if current phase not the same as prior then keep on with the current phase
            // then save that phase and initialize for a new phase
            if (priorPhase !== event.phase && priorPhase !== "") {
                phases[phaseIndex] = phaseObjects;
                priorPhase = event.phase;
                phaseObjects = [];
                phaseIndex = phaseIndex + 1;
                phaseNames[phaseIndex] = event.phase;
            }
            else {
                if (priorPhase === "") {
                    phaseNames[phaseIndex] = event.phase;
                    priorPhase = event.phase
                }
            }


            let tempType = tempObject.type
            // List of types in log that are currently processed.  Rest are filtered out.
            let processedTypes = ["assessment", "required-action", "intervention", "answer", "obtain-vital-sign", "decision-option", "assessment-option"]

            //skip over adding anything not of these types to the debriefing display for now
            if (!processedTypes.includes(tempType)){
                tempObject.processingState = "bad";
            }

            // when get an answer for an intervention, the intervention itself is later in the event log, so 
            // accumulate the answers and then when an intervention is later found we can use the correct label
            // and add the answers to it

            let priortempObject = [];
            if (tempType === "answer") {
                //currently returns correct answer-id if has answered incorrectly or false if answered correctly
                let wrongAnswer = evalForIncorrectAnswer(event.promptID, event.id)
                if (tempConfirmedActions.includes(tempObject.intervention)) {
                    priortempObject = phaseObjects.pop();
                    tempObject.answerCorrect = priortempObject.answerCorrect;
                    if (wrongAnswer) {
                        tempObject.answerCorrect = false;
                        let answerLabel = getCorrectAnswerLabel(event.promptID);
                        if (priortempObject.actionDescription === "") {
                            if (answerLabel !== ""){                 
                               tempObject.actionDescription = "should have specified: " + tempObject.prompt + " as: " + answerLabel;}
                            else {tempObject.actionDescription = "currently unable to assess responses: " + tempObject.prompt;}
                        }
                        else { 
                            if (answerLabel !== ""){
                                tempObject.actionDescription = priortempObject.actionDescription + ", " + tempObject.prompt + " as: " + answerLabel}
                            else {tempObject.actionDescription = "currently unable to assess responses for: " + tempObject.prompt;}}
                    }
                    else {
                        tempObject.actionDescription = priortempObject.actionDescription;
                        tempObject.answerCorrect = priortempObject.answerCorrect
                    }
                }
                else {
                    if (wrongAnswer) {
                        let answerLabel = getCorrectAnswerLabel(event.promptID);
                        tempObject.answerCorrect = false
                        if (answerLabel !== ""){
                          tempObject.actionDescription = "should have specified: " + tempObject.prompt + " as: " + answerLabel;}
                        else {tempObject.actionDescription = "currently unable to assess responses for: " + tempObject.prompt;}
                    }

                    else { tempObject.actionDescription = "" }
                    tempConfirmedActions.push(tempObject.intervention);
                }
            }

            if (tempType === "intervention") {
                priortempObject = phaseObjects.slice(-1)[0];  //slice returns an array rather than the last object in array
                if (priortempObject && priortempObject.type === "answer" && priortempObject.intervention === tempObject.id) {
                    priortempObject = phaseObjects.pop();
                    tempObject.answerCorrect = priortempObject.answerCorrect;
                    if (priortempObject.actionDescription === "") {
                        tempObject.actionDescription = tempObject.label
                    }
                    else {
                        tempObject.actionDescription = tempObject.label + ", " + priortempObject.actionDescription
                    }
                }
            }

            if (tempObject.processingState === "good") {

                // currently we only need to examine critical actions if the action 
                // is of type intervention

                if (tempObject.type === "intervention") {

                    // iterate through all problem actions looking how to format the
                    // data structue for the current "event"

                    

                    problems.forEach((prob, j) => {


                        // We're going through things in the order standard, contraindicated since
                        // it is possible but unlikely to have multiple matches and we want the last one to win.
                        // Also, we are assuming minimal is always a subset of standard

                        const PROBLEM_ACTION_FIELDS = ["findings", "protocol", "phase", "why"]

                        // check for this action to be an intervention for a problem
                        prob.actions?.forEach((action, k) => {
                            if (action.id === tempObject.id) {
                                if (!action.optional) {tempObject.required = true}
                                if (tempObject.answerCorrect) {
                                    tempObject.status = "standard";
                                }
                                else { tempObject.status = "standard-incorrect-answers" }
                                tempObject.probLabel = prob.probLabel
                                PROBLEM_ACTION_FIELDS.forEach(fldName => {
                                    if (!!action[fldName]) {
                                        tempObject[fldName] = action[fldName]
                                    }
                                })
                                if (!!action["id-label"]){tempObject["label"] = action["id-label"]}
                            }
                        })


                        // check for this action to be a contraindication for a problem solution
                        prob.contraindications?.forEach((contra, j) => {
                            if (contra.id === tempObject.id) {
                                tempObject.status = "contraindicated";
                                if (!!contra.why) {tempObject.contraindicatedWhy = contra.why}
                                if (!!contra["id-label"]) {tempObject.label = contra["id-label"]}
                                if (!!contra.protocol) {tempObject.protocol = contra.protocol}
                                if (!!prob.probLabel){tempObject.probLabel = prob.probLabel}
                            }
                        })

                    })
                    // add the formatted object to our list of formatted objects for the current phase and the id to our set of confirmed ids
                }
                tempConfirmedActions.push(tempObject.id);
                phaseObjects.push(tempObject);

            }
        })

        // store the last phase processed
        phases[phaseIndex] = phaseObjects;
        //console.log(phases)

        // sts 08/13/21 end of categorizeAndAnnotateActions


        // sts 08/13/21 start of categorizeAndAnnotateMissingActions()

        //add missing phase names to phaseNames
        requiredPhaseNames.forEach((phase, i) => {
            if (!phaseNames.includes(phase)) {
                phaseNames.splice(i, 0, phase);  //insert it where it should have appeared
                phases.splice(i, 0, []);  //add the empty array for phase objects
            }
        })


        // check for missing checklist steps
        let indexCounter = events.length
        var checklistSteps = scenario.checkListMetaData.filter(md => [MDB_TYPES.ASSESS, MDB_TYPES.REQUIRED].includes(md.type))
        //console.log("checkliststeps: ", checklistSteps)

        // before evaluating assessment steps, deal with exceptional cases where one or more steps is not
        // necessary for a given scenario.  Because GCS value is a string and not a formatted object
        // we can't reliably parse out values and units to do more general checks so can only set what 
        // steps should not be assessed for a particular scenario.  Requires info on feedback to give as
        // well.

        

        // Get any exceptional steps associated with the current scenario
        var exceptions = [];
        for (let exceptionDef of exceptionDefs) {
            if (exceptionDef.scenarioName === scenario.name){
                exceptions = exceptionDef.exceptions
                break;
            }}

        exceptions.forEach((exception, j) => {
                // remove the step from the checklist as it is not required 
                checklistSteps = checklistSteps.filter(entry => entry.id !== exception[0])

                // if the step was done during the observation then mark it as an issue
                if (tempConfirmedActions.includes(exception[0])) {
                    //console.log("marking action as not necessary")

                    // it could appear in any phase so check all phases and mark 
                    // each instancs as unnecessary
                    phases.forEach((phaseObjects,j) => {
                        phaseObjects.forEach((action, i) => {
                            if (action.id === exception[0]){
                                //console.log("found action to mark")
                                action.why = exception[1];
                                action.status = "notNecessary";
                            }})})}

                })

// iterate through all checklist steps for ones for which we don't have formated objects



        checklistSteps.forEach((cls, i) => {
            if (!tempConfirmedActions.includes(cls.id)) {
                // add so we don't check for the missing checklist item again
                tempConfirmedActions.push(cls.id);
                let phaseObject = {
                    "comment": "",
                    "numericalID": indexCounter,
                    "status": "missingAssessment",
                    "contraindicatedWhy": "",
                    "minimalWhy": "",
                    "why": "",
                    "probLabel": "",
                    "protocol": "",
                    "actionDescription": "",
                    "timestamp": -1
                };

                //copy over the step fields that exist (type dependent)
                const STEP_FIELDS = ["id", "label", "finding", "phase", "subPhase", "type", "prompt"]
                STEP_FIELDS.forEach(fldName => {
                    if (!!cls[fldName]) {
                        phaseObject[fldName] = cls[fldName]
                    }
                })
                if (!!phaseObject.label)
                   {phaseObject.actionDescription = phaseObject.label}
                insertInPhaseInOrder(phaseNames, phases, phaseObject, i)
                indexCounter++;
            }
        })

        // search for missing interventions for each action
        // Doing this separately from the earlier actions loop because
        // we wanted to add in missing assessment steps first

        problems.forEach(prob => {

            
            const actions = prob.actions


            // now we just have to examine action ids to see what is missing
            actions.forEach((action, k) => {
                if (!tempConfirmedActions.includes(action.id)) {
                    tempConfirmedActions.push(action.id)
                    let phaseObject = {
                        "comment": "",
                        "numericalID": indexCounter,
                        "status": "",
                        "required": true,
                        "when": "",
                        "probLabel": prob.probLabel,
                        "minimalWhy": prob.minimalWhy,
                        "findingsNeeded": prob.findings,
                        "protocol": prob.protocols,
                        "actionDescription": "",
                        "type": "intervention",
                        "timestamp": -1
                    };
    
                    //copy over the step fields that exist (type dependent)
                    const ACTION_FIELDS = ["id", "phase", "protocol", "why", "optional", "minimal"]
                    ACTION_FIELDS.forEach(fldName => {
                        if (!!action[fldName]) {
                            phaseObject[fldName] = action[fldName]
                        }
                    })

                    //initialize some fields above that come from action if they are defined
                    if (!!action.optional){phaseObject.required = !action.optional}  
                    if (!!action["id-label"]){phaseObject.label = action["id-label"]}
                    if (!!action["id-label"]){phaseObject.actionDescription = action["id-label"]}
                    if (!!action.phase){phaseObject.when = action.phase}


                    if (phaseObject.required){
                        phaseObject.status = "missingRequired" 
                    }
                    else {
                        if (!phaseObject.optional){phaseObject.status = "missingStandard"} 
                        else {phaseObject.status = "missingOptional"}}
                    //assume actions are in the suggested order to solve a problem
                    //console.log(phaseNames,phases,phaseObject,actions,k)
                    if (phaseObject.status !== "missingOptional"){
                        insertInPhaseRelativeToSuggestedOrder(phaseNames, phases, phaseObject, actions, k);
                        indexCounter++;}
                }

            })
        })



        // sts 08/13/21 end of categorizeAndAnnotateMissingActions()


        // sts 08/13/21 start of addHeadersToAnnotatedActions()
        //
        // I think this bit of code could be simplified further by not modifying the original tmpFormattedActions, and simply iterating
        // over it and creating a new data structure, which if the current record has a different label, pushes a header prior to pushing
        // the current record into the new array

        // put the object phases together into one object to represent the annotated log
        tempFormattedActions = organizeLogDisplay(phaseNames, phases);

        // Now insert critical action headers;
        let priorCA = "N/A";

        let finalLog = [];

        tempFormattedActions.forEach((tempObject, j) => {

            if (tempObject.type === "intervention" && tempObject.probLabel && !(priorCA === tempObject.probLabel)) {
                //console.log("prior & current", priorCA, tempObject.probLabel)
                priorCA = tempObject.probLabel;
                let tempHeaderObject = {
                    "id": "interventionHeader",
                    "type": "header",
                    "numericalID": 1,
                    "status": "green",
                    "why": "",
                    "probLabel": tempObject.probLabel,
                    "protocol": ""
                }
                finalLog.push(tempHeaderObject);
            }
            finalLog.push(tempObject);
        })

        // now we analyze all that we've found in the log to see whether
        // all the actions were done for each problem and reset
        // the status if not all are there but should be counted
        // as minimal for the feedback.

        let pActions = [];  //here we will store the actual problem actions
        let pNames = [];  //here we will store the problem names for the above with same order
        let pIndex = 0;
        let pActIndex = -1;

        finalLog.forEach((action, j) => {
            if (action.status.includes("standard", "standard-incorrect-answers")) {  //only interventions have status of standard
                if (!pNames.includes(action.probLabel)) {
                    pActIndex = pIndex;
                    pNames[pActIndex] = action.probLabel;
                    pIndex++;
                }
                else { pActIndex = pNames.indexOf(action.probLabel) }
                let actions = pActions[pActIndex];
                if (!actions) { actions = []; }
                actions.push(action);
                pActions[pActIndex] = actions;
            }
        })
        //console.log(finalLog)

        // If all the minimal were found but not all the action were, then we
        // need to remark the action objects created for the minimal events
        // to be minimal instead of standard.  It isn't
        // appropriate to give this feedback if all actions are present.
        // Waited until now so that all the events are in one list instead of 
        // a list per phase,

        let processedIds = tempFormattedActions.map(event => event.id);
        let pIds = problems.map(a => a.probLabel);
        // console.log("caIds: ", caIds);
        pNames.forEach((pEntry, j) => {
            //get the action definition for this problem 
            let pIndex = pIds.indexOf(pEntry);
            let prob = problems[pIndex];
            let actionNeeded = prob.actions.filter(obj => !obj.optional).map(obj => obj.id)
            let minimalNeeded = prob.actions.filter(obj => obj.minimal).map(obj => obj.id)
            if (minimalNeeded) {  //no point in doing anything else if no minimals were defined
                let foundActions = pActions[j];
                let found = foundActions.map(a => a.id);
                //console.log("sn: ", actionNeeded, "mn: ", minimalNeeded, "found: ", found);
                let allActionFound = true;
                let allMinimalFound = true;
                for (let item of actionNeeded) {
                    if (!found.includes(item)) {
                        allActionFound = false;
                        break;
                    }
                }
                for (let item of minimalNeeded) {
                    if (!found.includes(item)) {
                        allMinimalFound = false;
                        break;
                    }
                }
                //console.log("all action? ", allActionFound, "all minimal? ", allMinimalFound)   
                let numericalIDsChanged = [];
                if (!allActionFound && allMinimalFound) {
                    minimalNeeded.forEach((foundId, j) => {
                        let pIndex = processedIds.indexOf(foundId)
                        let object = tempFormattedActions[pIndex];

                        //console.log("object to modify", object);
                        object.minimalWhy = prob.minimalWhy;
                        object.status = "minimal";
                        numericalIDsChanged.push(object.numericalID);
                        tempFormattedActions[pIndex] = object;
                    })
                    //redo color for header if needed
                    let header = {};
                    numericalIDsChanged.forEach((numericalID, z) => {
                        header = findHeaderFor(numericalID, tempFormattedActions);
                        if (header && header.status === "green") { header.status = "yellow" }
                    })

                }
            }

        })


        console.log("Final log:", finalLog);
        setFormattedActions(finalLog);
        // end of addHeadersToAnnotatedActions()


    }


    // eslint-disable-next-line
    useEffect(
        () => {
            processEvents()
            // eslint-disable-next-line
        }, [] // empty list means this code will only be run on initial render
    )



    const dispHeaderRow = (entry) => {
        return (
            <>
                {entry.id ===  "assessmentHeader"
                    ? <td colSpan="4" className={getColor(entry)}>
                        {entry.label}
                    </td>
                    : <td colSpan="10" className={"black"} >
                        <div><em>
                        &nbsp;&nbsp;&nbsp;Intervention Group: {entry.probLabel}</em></div>
                    </td>}
            </>
        )
    }

    // all the details to display for a log entry given type and details available
    const getLabel = (entry) => {
        let dLabel = "";
        if (entry.type === "obtain-vital-sign") {
            dLabel = "requested " + entry.vital + ", found = " + entry.value
        }
        else {
            dLabel = entry.type
            if (entry.subPhase && entry.type !== "intervention") {
                dLabel = dLabel + "/" + entry.subPhase;
            }
            dLabel = dLabel + ": " + entry.actionDescription
            if (entry.finding) {
                dLabel = dLabel + ".  Finding: " + entry.finding
            }
        }

        return dLabel

    }

     function saveComment(val) {
        let orig = val.target.defaultValue;
        let comment = val.target.value;
        let entryID = val.target.id;
        if (orig !== comment){
            let entry = formattedActions.find(entry => (entry.id === entryID))
            // if (entry) { entry.comment = comment }}
            const newEntry = Object.assign({}, entry, {comment: comment})
            updateFormattedAction(newEntry)
        }
    }

    const dispDataRow = (entry) => {
        return (
            <>
                <td>{entry.numericalID}</td>
                <td>{getTimestamp(entry)}</td>
                <td className={getColor(entry)}>
                    <span className="material-icons-outlined">{getAssessmentIcon(entry)}</span>
                    {getLabel(entry)}
                </td>
                <td>{getFeedback(entry)}</td>
                {showCommentColumn && <td><input type="text" defaultValue={entry.comment} onBlur={saveComment} id={`${entry.id}`}/></td>}
            </>
        )
    }

    if (null === formattedActions) {
        return (<h3>processing</h3>)
    }
    return (
        <>
            <MDBNavBar
                title={`Document: ${log.label}`}
                leftNav={
                    <Button
                    color="primary"
                    onClick={saveDebriefingLog}>Save Debriefing Log
                    </Button> 
                    //<Link to="/">Back</Link>
                }
                rightNav={
                    <Button
                        color="primary"
                        onClick={toggleCommentColumn}>
                        {(showCommentColumn) ? "Hide comment column" : "Show comment column"}
                    </Button>
                }
            />
            <form>
                <div style={{ overflowY: "auto", maxHeight: "700px" }}>
                    <Table borderless size="sm">
                        <thead>
                            <tr>
                                <th className="table-header"> ID </th>
                                <th className="table-header"> Timestamp </th>
                                <th className="table-header"> Action Description </th>
                                <th className="table-header"> Feedback </th>
                                {showCommentColumn && <th className="table-header"> Comments </th>}
                            </tr>
                        </thead>
                        <tbody className="scrolling">
                            {formattedActions.map((currentEntry, index) => (
                                <tr key={index}>
                                    {currentEntry.type === "header"
                                        ? dispHeaderRow(currentEntry)
                                        : dispDataRow(currentEntry)
                                    }
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </div>
            </form>
        </>
    )
}


export default Debriefing